/* Bu blokta Biondiâ€™yi tamamen kaldÄ±rÄ±yoruz */
.hero-overlay,
.hero-overlay * {
  font-family: 'Encode Sans Expanded', sans-serif !important;
  font-weight: 300 !important;
}

/* Overlay: slider gÃ¶rseli Ã¼zerinde, sayfa iÃ§ boÅŸluklarÄ±yla hizalÄ± */
.hero-overlay {
  position: absolute;
  bottom: clamp(24px, 10vh, 80px);
  left: clamp(24px, 6vw, 11.72vw);
  z-index: 10;

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  margin: 0;
  padding: 0;
  max-width: 40vw;
  color: #fff;
}

/* BaÅŸlÄ±k */
.hero-title {
  font-family: 'Encode Sans Expanded';
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: .04em;
  font-size: clamp(42px, 7vw, 80px);
  line-height: 1.05;
  margin: 0 0 18px 0;

  white-space: nowrap;
  width: fit-content;

  text-align: left;
  display: block;
  margin-left: 0;
}

/* Buton */
.hero-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;

  height: clamp(40px, 5vh, 10px);
  width: clamp(160px, 14vw, 100px);
  padding: clamp(8px, 1.5vh, 12px) clamp(16px, 3vw, 24px);

  background: #fff;
  color: #000;
  text-decoration: none;
  border: none;
  cursor: pointer;

  font-family: 'Encode Sans Expanded';
  font-weight: 300;
  letter-spacing: .06em;
  font-size: clamp(14px, 1.6vw, 18px);
  text-transform: uppercase;

  transition: background-color .25s ease, transform .15s ease;
}

.hero-button:hover {
  background-color: #eee;
  color: #000;
}

/* Mobil dÃ¼zen */
@media (max-width: 992px) {
  .hero-overlay {
    left: 50%;
    transform: translateX(-50%);
    padding-left: 16px;
    padding-right: 16px;
    align-items: center;
    text-align: center;
    margin-bottom: 32px;
  }

  .hero-title {
  font-family: 'Encode Sans Expanded';
  font-weight: 300;
  white-space: normal;
  width: 100%;
  font-size: clamp(20px, 7vw, 40px) !important;
  text-align: center; /* âœ… yatayda ortalar */
       
  margin-bottom: 12px !important;
}



