html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: 'Encode Sans Expanded', sans-serif;
}

/* Sayfa kenar boÅŸluklarÄ±nÄ± koruyan kapsayÄ±cÄ± */
.page-container {
  margin-left: 11.72vw;
  margin-right: 11.72vw;
}

/* BÃ¶lÃ¼m sarmalayÄ±cÄ± */
.section-wrapper {
  width: 100%;
  max-width: 1920px;
  background-color: #fff;
  box-sizing: border-box;
}

/* Ä°Ã§ dÃ¼zen */
.intro-section {
  display: flex;
  flex-direction: column;
  gap: clamp(16px, 4vh, 32px);
  text-align: center;
}

/* BaÅŸlÄ±k */
.intro-title {
  font-family: 'Encode Sans Expanded', sans-serif;
  font-weight: 300;
  font-size: clamp(32px, 6vw, 80px);
  width: 100%;
  text-align: center;
  margin-top: 130px;
  margin-bottom: 90px;
}

/* AÃ§Ä±klama */
.intro-description {
  font-family: 'Encode Sans Expanded', sans-serif;
  font-weight: 300;
  font-size: clamp(16px, 2vw, 20px);
  width: 100%;
  text-align: center;
  margin: 0;
}

/* Buton alanÄ± */
.intro-button-wrapper {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 90px;
  margin-bottom: 130px;
}

/* Projelerimiz bÃ¶lÃ¼mÃ¼ */
.custom-section {
  width: 100%;
  height: 302px;
  background-color: #212121;
  display: flex;
  align-items: center;
  justify-content: center;
}

.custom-text {
  font-family: 'Encode Sans Expanded', sans-serif;
  font-weight: 300;
  font-size: 80px;
  color: #fff;
  text-align: center;
}

.project-row {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  margin: 0 !important;
  padding: 0 !important;
  gap: 0 !important;
}

.project-row>.ant-col {
  padding: 0 !important;
  margin: 0 !important;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.project-row.reverse {
  flex-direction: row-reverse;
}

.project-image {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  aspect-ratio: 12 / 5;
}

.project-info-title,
.project-info-description,
.project-info-button {
  margin-top: 0;
  margin-bottom: 0;
}

/* Ä°letiÅŸim bÃ¶lÃ¼mÃ¼ */
.contact-section {
  width: 100%;
  height: clamp(400px, 60vh, 629px);
  background-color: #f8f8f8;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.contact-content {
  text-align: center;
  max-width: 800px;
  width: 100%;
}

.contact-title {
  font-family: 'Encode Sans Expanded', sans-serif;
  font-weight: 300;
  font-size: clamp(32px, 6vw, 80px);
  color: #000;
  margin: 0;
  white-space: nowrap;
}

.contact-description {
  margin-top: clamp(24px, 4vh, 76px);
  margin-bottom: clamp(24px, 4vh, 76px);
  font-family: 'Encode Sans Expanded', sans-serif;
  font-weight: 300;
  font-size: clamp(16px, 2vw, 20px);
  color: #000;
  margin-left: auto;
  margin-right: auto;
}

/* Proje detayÄ± butonu */
.project-detail-button {
  width: clamp(100px, 35vw, 180px);
  height: clamp(36px, 6vw, 52px);
  font-size: clamp(14px, 4vw, 20px);
  font-weight: 300;
  background-color: #000;   /* normalde siyah */
  color: #fff;              /* yazÄ± beyaz */
  border: 2px solid #000;   /* border ekledik */
  transition: all 0.3s ease;
  font-family: 'Encode Sans Expanded', sans-serif;
  cursor: pointer;
}

/* Hover efekti â†’ outline gÃ¶rÃ¼nÃ¼m */
.project-detail-button:hover {
  background-color: #fff;   /* arka plan beyaz */
  color: #000;              /* yazÄ± siyah */
  border: 2px solid #000;   /* border siyah */
}
.iletisimButton {
  width: clamp(100px, 35vw, 180px) !important;
  height: clamp(36px, 6vw, 52px) !important;
  margin-top: 20px;
  font-size: 20px !important;
  background-color: #000;
  color: #fff;
  border: 2px solid #000;     /* baÅŸtan border olsun */
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease; /* sadece bunlarÄ± animasyonla */
  box-sizing: border-box;     /* border dahil */
}

.iletisimButton:hover {
  background-color: #fff !important;
  color: #000 !important;
  border: 2px solid #000 !important;
}

/* ====== Mobile ====== */
@media (max-width: 768px) {
  .page-container {
    margin-left: 24px;
    margin-right: 24px;
  }
.project-info-title{
  color: #fff;
}
  .intro-section {
    gap: 24px;
  }

  .intro-title {
    font-size: 32px;
    margin-top: 60px;
    margin-bottom: 60px;
  }

  .intro-description {
    font-size: 16px;
  }

  .intro-button-wrapper {
    margin-top: 60px;
    margin-bottom: 60px;
  }

  .custom-section {
    height: 145px;
    padding: 32px 0;
    text-align: center;
  }

  .custom-text {
    font-size: 36px;
  }

  .project-row {
    flex-direction: column !important;
    row-gap: 24px;
  }

  .project-row>.ant-col {
    padding: 0 16px;
  }

  .project-image {
    height: 350px;
    aspect-ratio: unset;
  }

  .contact-section {
    padding: 0px 24px !important;
    flex-direction: column;
  }

  .contact-title {
    font-size: 32px;
    white-space: normal;
  }

  .contact-description {
    font-size: 16px;
    margin-top: 24px;
  }

  .project-detail-button {
    width: 120px;
    height: 40px;
    font-size: 16px;
  }
}